html,
body {
  margin: 0;
  padding: 0;
  max-width: 100%;
  max-height: 100%;
  overflow-x: hidden;

  *,
  *::before,
  *::after {
    box-sizing: border-box;
  }

  font-family: "Montserrat",
  sans-serif;
}

:root {
  line-height: 1.5;
  font-weight: 400;
  color: rgba(255, 255, 255, 0.87);
  background-color: rgb(210, 210, 210);
  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/* Disable pointer events */
img {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  pointer-events: none;
}

/* Cards fade in on scroll */
@keyframes fadeIn {
  from {
    opacity: 0
  }

  to {
    opacity: 1
  }
}

.fade-in {
  animation: fadeIn 0.5s ease-in-out
}

/*Simple loader*/
.photo-loading {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10%;
  background: rgb(230, 230, 230)
}

/* Stretching bar loader */
@keyframes stretch {

  0%,
  40%,
  100% {
    transform: scaleY(0.4);
  }

  20% {
    transform: scaleY(1);
  }
}

.stretching-bars-loader {
  display: flex;
  justify-content: center
}

.stretching-bars-loader>div {
  width: 4px;
  height: 20px;
  margin: 2px;
  background-color: rgb(150, 150, 150);
  animation: stretch 1.2s infinite ease-in-out
}

.stretching-bars-loader>div:nth-child(2) {
  animation-delay: -1.1s
}

.stretching-bars-loader>div:nth-child(3) {
  animation-delay: -1s
}

.stretching-bars-loader>div:nth-child(4) {
  animation-delay: -0.9s
}

.hidden {
  display: none
}

.error {
  color: rgb(100, 100, 100);
  font-size: 2em;
  text-align: center;
  margin-top: 100px;
  font-weight: 600
}

.error a {
  color: rgb(100, 100, 100);
  text-decoration: none
}

@media screen and (max-width: 768px) {
  .error {
    font-size: 1.5em
  }
}

a {
  text-decoration: none;
  background-color: transparent;
  outline: none
}

a,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: inherit
}

button {
  border: none;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  cursor: pointer;
  transition: border-color 0.25s
}

button:hover {
  transform: translateY(1px)
}

button:focus,
button:focus-visible,
button:active {
  outline: none;
  box-shadow: none
}