/* SocialLinks.css */
.social-icon:hover {
  color: rgb(255, 255, 255)
}

.error .social-icon {
  color: rgb(100, 100, 100);
  padding: 0 10px;
  font-size: 2em;

  &:hover {
    color: rgb(150, 150, 150)
  }
}

.main-landing .user-buttons {
  display: flexbox;
  justify-content: center;
  position: relative;
  width: 100%;
  text-align: center;
  padding-top: 1rem
}

.main-landing .user-buttons h2 {
  justify-content: center;
  cursor: default;
  color: rgb(150, 150, 150)
}

.main-landing .social-icon {
  justify-content: center;
  color: rgb(150, 150, 150);
  padding: 0 0.8rem;
  font-size: 2em
}

.about-content .user-buttons {
  display: flex;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 1rem
}

.about-content .user-buttons .social-icon {
  color: rgb(130, 130, 130);
  padding: 0 0.8rem;
  font-size: 2em
}

/* Responsive adjustments */
@media (max-width: 1080px) {
  .main-landing .user-buttons {
    padding-top: 2rem;
    padding-bottom: 2rem
  }

  .main-landing .user-buttons h2 {
    font-size: 0.8em
  }

  .main-landing .social-icon {
    font-size: 1.5em
  }


}

@media (min-width: 768px) and (max-width: 1366px) {
  .main-landing .user-buttons h2 {
    font-size: 1.5em
  }

  .main-landing .social-icon {
    font-size: 2em
  }

}